.card {
  /* background: #fff; */
  border-radius: 2px;
  /* height: 325px; */
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(98, 155, 219);
}
.card > .img-container:hover {
  background:rgb(242, 180, 94);
}
