.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  background: rgb(38,4,61);
  background: -webkit-linear-gradient(top, rgba(38,4,61,1) 0%, rgba(121,9,9,1) 53%, rgba(57,55,140,1) 100%);
  background: linear-gradient(180deg, rgba(38,4,61,1) 0%, rgba(121,9,9,1) 53%, rgba(57,55,140,1) 100%);
}

.title {
margin-left: auto;
margin-right: auto;
}

.scorecard {
  /* background-image: url('https://purepng.com/public/uploads/large/purepng.com-super-smash-bros-logo-newsuper-smash-brose3logonintendoblack-and-whitemodern-21528619282yzmut.png');
  background-size:60% 60%; */
  background: rgb(0,0,0);
background: -webkit-linear-gradient(top, rgba(0,0,0,1) 0%, rgba(38,4,61,1) 100%, rgba(121,9,9,1) 100%, rgba(57,55,140,1) 100%);
background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(38,4,61,1) 100%, rgba(121,9,9,1) 100%, rgba(57,55,140,1) 100%);
  color: whitesmoke;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

